namespace $ { export class $mpk_tss_parameter_speed extends $mol_view {

	/**
	 * ```
	 * sub /
	 * 	<= value
	 * 	<= spacer
	 * 	<= unit
	 * ```
	 **/
	sub() {
		return [].concat( this.value() , this.spacer() , this.unit() )
	}

	/**
	 * ```
	 * value 0
	 * ```
	 **/
	value() {
		return 0
	}

	/**
	 * ```
	 * spacer \
	 * ```
	 **/
	spacer() {
		return " "
	}

	/**
	 * ```
	 * unit @ \km/h
	 * ```
	 **/
	unit() {
		return this.$.$mol_locale.text( "$mpk_tss_parameter_speed_unit" )
	}

} }

